import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children: [
        {
          name: 'Home',
          path: 'home',
          component: () => import('@/view/Home'),
          alias: '/'
        },
        {
          name: 'HowDoWork',
          path: 'howDoWork',
          component: () => import('@/view/HowDoWork')
        },
        {
          name: 'Privacy',
          path: 'privacy',
          component: () => import('@/view/Privacy')
        },
        {
          name: 'Heroes',
          path: 'heroes',
          component: () => import('@/view/people/Heroes')
        },
        {
          name: 'GettingStarted',
          path: 'gettingStarted',
          component: () => import('@/view/GettingStarted')
        },
        {
          name: 'Parzival',
          path: 'parzival',
          component: () => import('@/view/people/Parzival')
        },
        {
          name: 'Art3mis',
          path: 'art3mis',
          component: () => import('@/view/people/Art3mis')
        },
        {
          name: 'Aech',
          path: 'aech',
          component: () => import('@/view/people/Aech')
        },
        {
          name: 'Daito',
          path: 'daito',
          component: () => import('@/view/people/Daito')
        },
        {
          name: 'Shoto',
          path: 'shoto',
          component: () => import('@/view/people/Shoto')
        },
        {
          name: 'Game',
          path: 'game',
          component: () => import('@/view/Game')
        },
        {
          name: 'Faq',
          path: 'faq',
          component: () => import('@/view/Faq')
        },
        {
          name: 'Ecology',
          path: 'ecology',
          component: () => import('@/view/Ecology')
        }
      ]
    },
    {
      name: 'Test',
      path: '/test',
      component: () => import('@/components/common/Test')
    },
    {
      name: 'NotFound',
      path: '/404',
      component: () => import('@/components/common/NotFound')
    },
    {// 路由最下方为404页面
      path: '*',
      redirect: '/404'
    }
  ]
})

// 全局前置守卫
router.beforeEach((to, from, next) => {
  // 判断 vuex中的值是否还在
  next()
})

// 全局后置守卫
router.afterEach(async (to, from) => {
})

export default router



