<template>
  <!-- 页眉组件 -->
  <div class="header">
    <!-- 电脑端 -->
    <header id="headerPC">
      <div class="logo">
        <a href="#"><img src="../../assets/img/logo2.png" alt=""></a>
      </div>
      <nav id="navPC">
        <ul>
          <li><router-link to='/'>{{$t('header.home')}}</router-link></li>
          <li><router-link to='gettingStarted'>{{$t('header.gettingStarted')}}</router-link></li>
          <li><a href="https://kocgame.github.io" target="_blank">{{$t('header.game')}}</a></li>
          <li><router-link to='faq'>FAQ</router-link></li>
          <li><router-link to='ecology'>{{$t('header.ecology')}}</router-link></li>
          <li><a @click="openWhitePaper()" id="whitePaperDL" href="/static/download/Key of contract whitepaper.pdf" target="_blank">{{$t('header.whitePaper')}}</a></li>
          <!-- download="whitePaper_PDF" -->
          <li>
              <select name="lang" id="lang" value='en' @change="switchLang()">
                <option value="en" selected>EN</option>
                <option value="cn">CN</option>
              </select>
          </li>
        </ul>
      </nav>
    </header>

    <!-- centerText 电脑端 -->
    <div class="centerText" id="centerTextPC">
      <div>
        <h1>{{$t('box.p')}}</h1>
        <p>{{$t('box.p1')}}</p>
        <p>{{$t('box.p2')}}</p>
        <p>{{$t('box.p3')}}</p>
        <p>{{$t('box.p4')}}</p>
        <div><router-link to="heroes">{{$t('box.p5')}}</router-link></div>
      </div>
    </div>
    <!-- 轮播图电脑端 -->
    <div class="rotateBox">
        <div class="swiper-container">
          <swiper ref="mySwiper" :options="swiperOption">
            <swiper-slide v-for="(itemImg,index) in itemImgs" :key="index">
              <img :src="itemImg.url" alt="swiper-image" class="headers">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
            <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
            <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
          </swiper>
        </div>
    </div>

    <!-- 头部手机端 -->
    <div id="headerMobile" class="hidden-sm-and-up">
      <!-- logo+动态导航栏 fixed布局固定 若内容宽度超出屏幕宽度则会造成fixed失效-->
      <div class="bar">
        <!-- 透明条 -->
        <div class="transparentBar"></div>
        <!-- 导航下拉菜单 -->
        <el-row class="block-col-2" id="popup">
          <el-col :span="12">
            <el-dropdown trigger="click">
              <span class="el-dropdown-link">
                Menu<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item icon="el-icon-s-home"><router-link to='/'>{{$t('header.home')}}</router-link></el-dropdown-item>
                <el-dropdown-item icon="el-icon-guide"><router-link to='gettingStarted'>{{$t('header.gettingStarted')}}</router-link></el-dropdown-item>
                <el-dropdown-item icon="el-icon-thumb"><a href="https://kocgame.github.io" target="_blank">{{$t('header.game')}}</a></el-dropdown-item>
                <el-dropdown-item icon="el-icon-s-promotion"><router-link to='faq'>FAQ</router-link></el-dropdown-item>
                <el-dropdown-item icon="el-icon-notebook-1"><router-link to='ecology'>{{$t('header.ecology')}}</router-link></el-dropdown-item>
                <el-dropdown-item icon="el-icon-reading"><a id="whitePaperDL" href="/static/download/Key of contract whitepaper.pdf" target="_blank">{{$t('header.whitePaper')}}</a></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
        <!-- 语言切换下拉菜单 -->
        <el-row class="block-col-2" id="popupLang">
          <el-col :span="12">
            <el-dropdown trigger="click">
              <span class="el-dropdown-link">
                {{$t('header.lang')}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item icon="el-icon-s-home"><a id="toEng" href="javascript:;" @click="switchLangMobile('en')">{{$t('header.en')}}</a></el-dropdown-item>
                <el-dropdown-item icon="el-icon-guide"><a id="toChi" href="javascript:;" @click="switchLangMobile('cn')">{{$t('header.cn')}}</a></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
        <!-- logo -->
        <div class="logoMobile">
          <a href="#"><img src="../../assets/img/logo2.png" alt=""></a>
        </div>
      </div>
      <!-- 背景图片 -->
      <div class="bgImgMobile">
        <img src="../../assets/img/img/rotate1.png" alt="">
      </div>
      <!-- 中心文字内容 -->
      <div class="centerTextMobile">
          <h1>{{$t('box.p')}}</h1>
          <p>{{$t('box.p1')}}</p>
          <p>{{$t('box.p2')}}</p>
          <p>{{$t('box.p3')}}</p>
          <p>{{$t('box.p4')}}</p>
      </div>
      <!-- 开始游戏按钮 -->
      <div class="playGame"><router-link to="heroes">{{$t('box.p5')}}</router-link></div>
    </div>
  </div>
</template>

<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import rotate1 from '../../assets/img/img/rotate1.png'
import rotate2 from '../../assets/img/img/rotate2.png'
import rotate3 from '../../assets/img/img/rotate3.png'
import rotate4 from '../../assets/img/img/rotate4.png'
import rotate5 from '../../assets/img/img/rotate5.png'
import 'element-ui/lib/theme-chalk/display.css'
export default {
  name: 'Header',
  data () {
    return {
      itemImgs: [
        {
          id: 0,
          url: rotate1
        },
        {
          id: 1,
          url: rotate2
        },
        {
          id: 2,
          url: rotate3
        },
        {
          id: 3,
          url: rotate4
        },
        {
          id: 4,
          url: rotate5
        }
      ],
      swiperOption: {
        notNextTick: true,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        loop: true,
        direction: 'horizontal',
        paginationType: 'bullets',
        paginationClickable: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        preventClicks: true// 默认true
      }
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  created () {
    this.$store.commit('i18n/switchLang', 'en') // 初始化为英文版
  },
  methods: {
    switchLang () {
      let lang = document.getElementById('lang').value
      let whitePaperDL = document.getElementById('whitePaperDL')
      if (lang === 'en') {
        whitePaperDL.href = '/static/download/Key of contract whitepaper.pdf'
      } else if (lang === 'cn') {
        whitePaperDL.href = '/static/download/Key of contract whitepaper CN.pdf'
      }
      this.$store.commit('i18n/switchLang', lang)
    },
    switchLangMobile (lang) {
      let whitePaperDL = document.getElementById('whitePaperDL')
      if (lang === 'en') {
        whitePaperDL.href = '/static/download/Key of contract whitepaper.pdf'
      } else if (lang === 'cn') {
        whitePaperDL.href = '/static/download/Key of contract whitepaper CN.pdf'
      }
      this.$store.commit('i18n/switchLang', lang)
    },
    openWhitePaper () {
      let whitePaperDL = document.getElementById('whitePaperDL')
      window.open(`/pdf/web/viewer.html?file=${whitePaperDL.href}`, 'PDF', 'width:50%;height:50%;top:100;left:100;')
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss">
//general
// @media screen and (min-width: 640px){
  ul{
    padding: 0;
    margin: 0;
  }
  li{
    list-style: none;
  }
  li a{
    text-decoration: none;
  }
  .header{
    width: 100%;height: auto;
  }
  // 导航栏
  header#headerPC{
    width: 70%;height: 200px;
    min-width: 768px;
    position: absolute;
    left: 50%;
    margin-left: -35%;
    font-size: 18px;
    color: white;
    z-index: 1999;
  }
  .logo{
    position: absolute;
    left: 20px;top: 50px;
  }
  nav{
    position: absolute;
    right: 20px;
    top: 50px;
  }
  nav ul{
    display: inline-block;
    height: 38px;
    font-size: 0;
  }
  nav li{
    font-size: 18px;
    border-radius: 10px;
    display: inline-block;
    cursor: pointer;
  }
  nav li a{
    color: white;
    display: inline-block;
    padding: 10px 20px;
    border-radius: 10px;
  }
  li:hover{
    background-color: rgb(180,180,180);
  }
  // 中间d文本
  .centerText{
    position: absolute;
    top: 300px;left: 50%;
    width: 500px;height: 300px;
    margin-left: -250px;
    text-align: center;
    z-index: 10000;
  }
  .centerText>div>div{
    margin: 0 auto;
    line-height: 50px;
    font-weight: bolder;
    background-color: goldenrod;
    cursor: pointer;
    width: 200px;height: 50px;
  }
  .centerText>div>div a{
    text-decoration: none;
    color: black;
    display: block;
  }
  .centerText h1{
    color: white;
  }
  .centerText p{
    color: #888;
  }
  // 轮播图
  .rotateBox{
    width: 100%;height: auto;
    min-width: 768px;
    z-index: 200;
  }
// }

// 移动端适配
@media screen and (max-width: 640px){
  html,body{
    width: 100%;height: 100%;
    font-size: 16px;
  }
  .header{
    position: relative;
    width: 100%;height: 200px;
  }
  #navPC,#centerTextPC,.rotateBox,#headerPC,.logo{  //禁用电脑端内容
    display: none;
  }

  //header
  .header>#headerMobile{
    width: 100%;height: auto;
    position: absolute;
  }
  //logo透明条 start
  .bar{
    width: 100%;height: auto;
    position: fixed;
    top: 0;
    z-index: 100;
  }
  .transparentBar{
    width: 100%;height: 40px;
    opacity: 0.8;
    background-color: rgb(23,23,23);
  }
  //导航下拉菜单
  #popup{
    z-index: 10000;
    position: absolute;
    top: 10px;right: 0px;
  }
  .el-dropdown-link {
    display: inline-block;
    width: 80px;
    cursor: pointer;
    color: #FFD64A;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .demonstration {
    display: block;
    color: #8492a6;
    font-size: 14px;
    margin-bottom: 20px;
  }
  #popupLang{
    z-index: 10000;
    position: absolute;
    top: 10px;left: 10px;
  }
  //logo透明条 end

  //logo start
  .logoMobile{
    width: 40px;height: 40px;
    position: absolute;
    top: 10px;left: 50%;
    margin-left: -20px;
    display: inline-block;
    z-index: 400;
  }
  .logoMobile img{
    width: 40px;height: auto;
  }
  //logo end

  //背景图片
  .bgImgMobile img{
    z-index: -10;
    width: 100%;height: auto;
  }
  //中间文字
  .centerTextMobile{
    position: absolute;
    top: 0;left: 50%;
    width: 200px;height: auto;
    margin-top: 3.3rem;
    margin-left: -100px;
    text-align: center;
  }
  .centerTextMobile h1{
    color: white;
    font-size: 16px;
    margin: 0;
    font-weight: normal;
  }
  .centerTextMobile p{
    color: #ddd;
    font-size: 12px;
    margin: 0;
  }
  // 开始游戏按钮
  .playGame{
    width: 100px;height: 30px;
    text-align: center;
    line-height: 30px;
    margin: 10px auto;
    cursor: pointer;
    background: linear-gradient(#f0dabf 0%,#fed64a 100%);
  }
  .playGame a{
    text-decoration: none;
    color: black;
  }
}
// ipad兼容
@media screen and (max-width: 768px){
  // 盒子大小
  #app > div > div.header > div.rotateBox > div > div > div.swiper-wrapper div{
    width: 100%!important;
  }
  header#headerPC{
    width: 768px;
    height: 200px;
    max-width: 768px;
    position: absolute;
    left: 35%;
    font-size: 14px;
    color: white;
    z-index: 1999;
  }
  .logo{
    left: 0;
  }
}
// 最小屏幕适配
@media screen and (max-width: 320px){
  html,body{
    font-size: 12px;
  }
}

</style>



