此外，数据主体有权获得有关个人数据是否转移到第三国或国际组织的信息。在这种情况下，数据主体有权被告知与转移有关的适当保障措施。如果数据主体希望利用此访问权限，他或她可以随时联系控制器的任何员工。

c）纠正权

每个数据主体都有权由欧洲立法者授予从控制人那里获得的权利，不得无故拖延地纠正与他或她有关的不准确的个人数据。考虑到处理的目的，数据主体有权完成不完整的个人数据，包括提供补充声明。如果数据主体希望行使此权利进行纠正，他或她可以随时联系管理人员。

