每个数据主体应有权由欧洲立法者授权从控制器获得有关他或她的个人数据的擦除而不得无故拖延，并且管理者有义务在没有不当延迟的情况下删除个人数据，其中一个以下理由适用，只要不需要处理：

个人数据不再需要与收集或以其他方式处理的目的相关。

根据GDPR第6（1）条（a）点或GDPR第9（2）条（a）点，以及其他法律依据，数据主体撤回处理所依据的条目用于处理。

