<template>
  <div class="index">
    <Header/>
    <router-view/>
    <Footer/>
  </div>
</template>

<script>
import Header from '../components/common/Header'
import Footer from '../components/common/Footer'
export default {
  name: 'Index',
  data () {
    return {
    }
  },
  mounted () {
    // 编写一个javscript函数 fn，该函数有一个参数 n（数字类型），其返回值是一个数组，该数组内是 n 个随机且不重复的整数，且整数取值范围是 [2, 32]。
    // 获取滚动条当前的位置
    let t = 0
    let p = 0
    function getScrollTop () {
      var scrollTop = 0
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop
      } else if (document.body) {
        scrollTop = document.body.scrollTop
      }
      return scrollTop
    }

    // 获取当前可视范围的高度
    // function getClientHeight () {
    //   var clientHeight = 0
    //   if (document.body.clientHeight && document.documentElement.clientHeight) {
    //     clientHeight = Math.min(document.body.clientHeight, document.documentElement.clientHeight)
    //   } else {
    //     clientHeight = Math.max(document.body.clientHeight, document.documentElement.clientHeight)
    //   }
    //   return clientHeight
    // }

    // // 获取文档完整的高度
    // function getScrollHeight () {
    //   return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight)
    // }

    // 滚动事件触发
    window.onscroll = function () {
      p = getScrollTop()
      if (p > t) { // getScrollTop() + getClientHeight() + 1 > getScrollHeight() - 250
        if ((navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i))) {
          document.getElementsByClassName('footer')[0].setAttribute('style', 'display:none;')
        }
      } else {
        if ((navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i))) {
          document.getElementsByClassName('footer')[0].setAttribute('style', 'display:static;')
        }
      }
      setTimeout(function () { t = p }, 0)
    }
  },
  components: {
    Header,
    Footer
  }
}
</script>

<style lang="scss">
html,body{
  color: black;
  width: 100%;height: 100%;
  background-color: #1c1b1b;
}
div,li{
  box-sizing: border-box;
}
.index{
  min-height: 100%;
  overflow: scroll;
}
@media screen and (max-width: 640px){

}
</style>



