import i18n from '../../utils/i18n'

const state = {
  lang: null
}

const mutations = {
  switchLang (state, lang) {
    // console.warn('开始语言切换========', lang)
    state.lang = lang
    i18n.locale = lang
    localStorage.lang = lang
  }
}

export default {
  namespaced: true,
  state,
  mutations
}



