<template>
  <!-- 页脚组件 -->
  <div class="footer">
    <div class="joinUs">
      <div>
        <div class="code"><img src="../../assets/img/code.jpg" alt=""></div>
        <div class="joinUsContent">
          <h2>{{$t('footer.communityy')}}</h2>
          <div>
            <p>{{$t('footer.joinUs')}}</p>
            <div class="communityTextBox">
              <h3>{{$t('footer.Disclaimer')}}</h3>
              <p>{{$t('footer.DisclaimerText')}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="copyRight">
      <router-link to="privacy">{{$t('footer.copyRight')}}</router-link>
    </div>
    <!-- 移动端 -->
    <div class="hidden-sm-and-up" id="footerMobile">
      <div class="joinUsMobile">
        <div><!-- logo+join us -->
          <div class="codeMobile"><img src="../../assets/img/code.jpg" alt=""></div>
          <div>
            <h2>{{$t('footer.communityy')}}</h2>
            <p>{{$t('footer.joinUs')}}</p>
          </div>
        </div>
        <div class="communityTextBoxMobile"><!-- 免责声明 -->
          <h3>{{$t('footer.Disclaimer')}}</h3>
          <p>{{$t('footer.DisclaimerText')}}</p>
        </div>
      </div>
      <div class="copyRightMobile">
        <router-link to="privacy">{{$t('footer.copyRight')}}</router-link>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'Footer'
}
</script>

<style lang="scss">
// general
.footer{
  text-align: center;
}
// joinUs start
.joinUs{  // 外层容器
  width: 100%;height: 484px;
  background: url('../../assets/img/footerBg.png') no-repeat;
  display: flex;
  align-items: center;
  text-align: center;
}
.joinUs>div{  // 居中盒子
  width: auto;min-width: 750px;
  display: flex;
  margin: 0 auto;
}
.code{  // 二维码盒子
  z-index: 1999px;
  flex: 0.4;
  min-width: 200px;height: 200px;
  text-align: center;
  display: flex;
  align-items: center;
  margin-top: 50px;
}
.code img{  // 二维码图片
  width: 150px;height: 150px;
}
.joinUsContent h2{
  color: #D3BB7B;
  font-size: 24px;
  text-align: center;
}
.joinUsContent h2+div{
  margin-top: 20px;
  color: white;
  text-align: left;
  width: 430px;
}
// joinUs end

//copyright start
.copyRight{
  text-align: center;
  height: 40px;line-height: 40px;
  display: flex;
  align-items: center;
  background: url('../../assets/img/paper.png') repeat;
}
.copyRight a{
  margin: 0 auto;
  color: gray;
  text-decoration: none;
  min-width: 950px;
  overflow: hidden;
}
//copyright end

// 移动端适配
@media screen and (max-width: 640px){
  .footer{
    width: 100%;
    height: 217px;
  }
  .joinUs,.copyRight{
    display: none;
  }
  #footerMobile{
    width: 100%;height: auto;
    // display: flex;
    // position: relative;
    z-index: 20000;
    position: fixed;
    bottom: 0;
  }
  .joinUsMobile{
    width: 100%;height: auto;
    display: flex;
    position: absolute;
    bottom: -1px;
    background: url('../../assets/img/paper.png') repeat;
  }
  .joinUsMobile>div:first-of-type{
    width: 100%;height: auto;
    position: absolute;
    bottom: 128px;  // adjust height 66
    background: black;
    background-size: cover;
    display: flex;
    z-index: 2000;
  }
  .joinUsMobile>div>div:first-of-type{
    display: inline-block;
    flex:0.2;
    min-width: 50px;height: auto;
    display: flex;
  }
  .joinUsMobile>div>div:first-of-type img{
    width: 50px;height: 50px;
    margin: 0 auto;
    align-self: center;
  }
  .joinUsMobile>div>div:last-of-type{
    flex: 0.8;
    display: inline-block;
    text-align: center;
    margin: 10px auto;
    color: #ccc;
  }
  .joinUsMobile>div>div:last-of-type h2{
    font-size: 14px;
    margin: 0;
    color: #D3BB7B;
  }
  .joinUsMobile>div>div:last-of-type p{
    font-size: 13px;
    margin: 0;
  }
  .communityTextBoxMobile{
    width: 100%;height: 120px;
    background: url('../../assets/img/paper.png') repeat;
    color: #ccc;
    font-size: 12px;
    position: absolute;
    bottom: 10px; // adjust
  }
  .communityTextBoxMobile p,.communityTextBoxMobile h3{
    margin: 5px;
  }
// joinUs end 33 66

//copyright start
.copyRightMobile{
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  height: 11px;line-height: 11px;
  display: flex;
  align-items: center;
  background: url('../../assets/img/paper.png') repeat;
}
.copyRightMobile a{
  margin: 0 auto;
  font-size: 11px;
  color: gray;
  text-decoration: none;
}
//copyright end
}
//跟宽度影响到的行数影响高度做的移动兼容
@media screen and (max-width: 390px){
  html,body{
    font-size: 12px;
  }
  .joinUsMobile>div:first-of-type{
    bottom: 125px;
  }
  .communityTextBoxMobile{
    bottom: 11px;
  }
}
// 最小屏幕适配
@media screen and (max-width: 320px){
  html,body{
    font-size: 12px;
  }
  .joinUsMobile>div:first-of-type{
    bottom: 125px;
  }
  .communityTextBoxMobile{
    bottom: 11px;
  }
}
</style>



