  work: {
    work: 'How does this work?',
    content: `  The rules of this token are simple yet also brilliant. Quick summary:

  Buy Tokens. Buy KOC tokens, and 10% of your transaction is used to pay earnings to other users.

  Accumulate Earnings. Accumulate Ethereum earnings as other users buy, sell, or transfer KOC.

  Reinvest or Withdraw. Reinvest or withdraw your Ethereum earnings as they accumulate.

  Sell Tokens or Don't! Sell your tokens at any time to receive Ethereum back from the exchange.

  Share your Masternode. If you buy enough tokens for a masternode, share your masternode link to get bonus earnings!

  Have Fun! Have fun watching as your earnings and token value fluctuate over time!

  As soon as you exchange the KOC tokens for your Ethereum, there is a 10% exchange tax incurred. This means if you exchange 10ETH into KOC the exchange will provide you 9ETH worth of tokens due to that tax.Unlike a normal exchange, that 10% exchange tax is then completely distributed to all other token holders that have used this exchange. The distribution of earnings is divided fairly based on the number of KOC tokens each user holds. The site owners directly keep none of that tax, this all goes to other users.

  Now that you hold KOC tokens, every single time a new person uses the exchange to receive KOC tokens, you also get paid your share of the 10% exchange tax on their transaction.

  Keep in mind though that all earnings are paid directly in ETH. In this way, you collect new earnings in real-time on every buy.Even better, you also collect earnings from other types of transactions such as other users selling or users transferring tokens. Yes, this means you are getting paid earnings directly in Ethereum every time a user buys, sells, or transfers tokens!

  As your personal earnings accumulate, you can check the site anytime to see how many earnings you've earned and then choose to either 'withdraw' or 'reinvest' your earnings.

  If you withdraw, you get the Ethereum earnings sent directly to your metamask wallet. If you reinvest, the Ethereum earnings are used to purchase yourself additional KOC tokens.

  Every time a new user purchases a token, the value of each token increases very slightly (+10 GWEI). 

  This means that as people buy into the exchange, your KOC tokens are worth more and more Ethereum. However, as people sell their tokens, the value of each token you hold goes down (-10 gwei).

  Continue accumulating KOC tokens by reinvesting (and/or withdrawing "free" Ethereum from your earnings).

  If you bought enough tokens for a masternode, share your masternode link to get bonus earnings.

  For every buy directly purchased from your masternode, you get an extra 3,33% of the buy-in amount.For example, if a friend buys 10 ETH from your masternode, you make 0.33 ETH in earnings right away!

  You can sell tokens at any time directly back to the exchange, and the exchange will provide you back Ethereum based on the current sell exchange price.

  Have fun watching as your earnings and coin value fluctuate over time!
`},
